@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700;900&display=swap);
*{padding:0;margin:0;border:0}*,*:before,*:after{box-sizing:border-box}:focus,:active{outline:none}a:focus,a:active{outline:none}nav,footer,header,aside{display:block}html,body{height:100%;width:100%;font-size:100%;line-height:1;font-size:14px;-ms-text-size-adjust:100%;-moz-text-size-adjust:100%;-webkit-text-size-adjust:100%}input,button,textarea{font-family:inherit}input::-ms-clear{display:none}button{cursor:pointer}button::-moz-focus-inner{padding:0;border:0}a,a:visited{text-decoration:none}a:hover{text-decoration:none}ul li{list-style:none}img{vertical-align:top}h1,h2,h3,h4,h5,h6{font-size:inherit;font-weight:400}

.wrapper{min-width:100vh;display:flex;flex-direction:column;font-family:"Lato", sans-serif}.title{font-size:34px;font-weight:700;text-transform:uppercase;letter-spacing:8.95px;position:relative;text-align:center;padding:0px 0px 30px 0px}.title h2:after{content:'';position:absolute;width:90px;height:2px;background-color:black;bottom:0;left:50%;margin:0px 0px 0px -50px}.button button{display:block;border:2px solid white;width:200px;height:50px;background-color:rgba(0,0,0,0);color:white;position:relative}

.home{min-height:100vh;position:relative;display:flex;flex-direction:column;align-items:center;justify-content:center}.home__background{position:absolute;top:0;left:0;z-index:-1;width:100%;height:100%;object-fit:cover}.home__content{flex:1 1 auto;display:flex;flex-direction:column;align-items:center}.home__logo{flex:1 1 33%;display:flex;flex-direction:column;justify-content:center;height:100%}.home__title{flex:0 0 auto;display:flex;flex-direction:column;justify-content:center;color:white}.home__title h2{display:block;font-size:20px;text-transform:lowercase;margin:10px 0px 0px 0px}.home__title h2:after{background-color:white}.home__button{flex:1 1 33%;display:flex;flex-direction:column;align-items:center;justify-content:center}.home__button button{transition-duration:0.5s}.home__button button:after{content:'';position:absolute;bottom:-75px;left:50%;width:17px;height:10px;background:url(/binary/static/media/Arrow-down.307890a9.svg) center no-repeat;margin:0px 0px 0px -8.5px}.home__button button:hover{box-shadow:0px 0px 10px #EAEAEA}.home__button button:hover:after{animation:button linear 1.3s infinite}.home__button button:active{background-color:rgba(255,255,255,0.2)}@keyframes button{0%{transform:translate(0px)}50%{transform:translate(0px, -10px)}100%{transform:translate(0px, 0px)}}

.navbar{min-height:60px;display:flex;justify-content:center;align-items:center;position:-webkit-sticky;position:sticky;top:0;z-index:1;border-bottom:2px solid #F2F2F2;background-color:white}.navbar__list{display:flex;font-size:15px;text-transform:uppercase}.navbar__list li{margin-right:60px;-webkit-user-select:none;-ms-user-select:none;user-select:none;cursor:pointer}.navbar__list li:last-child{margin-right:0}.navbar__list li:hover{color:#ADA074}

.about{padding:70px 40px 40px 40px;min-height:50vh;display:flex;flex-direction:column;align-items:center;justify-content:center}.about__title{margin-bottom:40px}.about__text{margin-bottom:70px;max-width:600px;color:#868585;font-weight:300;text-align:center;line-height:25.89px}.about__columns{display:grid;align-content:center;justify-content:center;grid-template-columns:repeat(auto-fit, minmax(200px, 1fr));width:80%;grid-gap:40px;gap:40px}.item{display:flex;flex-direction:column;align-items:center}.item__image{width:92px;height:92px;border:2px solid #f1f1f1;border-radius:50%;display:flex;align-items:center;justify-content:center}.item__title{text-align:center;color:black;font-size:22px;text-transform:uppercase;letter-spacing:3.52px;margin:15px 0px 35px 0px}.item__text{text-align:center;line-height:26px;font-weight:300;color:#868585;max-width:260px}

.portfolio{background-color:#F3F3F3;padding:70px 40px 40px 40px;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center}.portfolio__title{margin-bottom:50px}.portfolio__selector{display:flex;margin-bottom:130px}.portfolio__selector li{-webkit-user-select:none;-ms-user-select:none;user-select:none;cursor:pointer;margin-right:50px;text-transform:uppercase;font-size:15px}.portfolio__selector li:hover{color:#ADA074}.portfolio__selector li:last-child{margin-right:0}.portfolio__gallery{display:grid;grid-template-columns:repeat(2, 539px);grid-gap:26px;gap:26px;margin-bottom:60px}.portfolio__item{width:539px;height:366px;background-color:#EAEAEA}.portfolio__button button{text-transform:uppercase;border-color:black;color:black}.portfolio__button button:hover{box-shadow:0px 0px 10px rgba(0,0,0,0.3)}.portfolio__button button:active{background-color:rgba(0,0,0,0.2)}

.partners{min-height:50vh;position:relative;padding:40px;display:flex;flex-direction:column;justify-content:center}.partners__background{position:absolute;top:0;left:0;z-index:-1;width:100%;height:100%;object-fit:cover}.partners__content{display:flex;flex-direction:column;align-items:center;justify-content:center}.partners__title{font-size:30px;margin-bottom:90px}.partners__title h2{color:#FFFFFF}.partners__row{width:80%;display:grid;align-content:center;justify-content:center;grid-template-columns:repeat(auto-fit, minmax(125px, 1fr));grid-gap:20px;gap:20px}.partners__item{display:flex;align-items:center;justify-content:center}.partners__image img{max-width:100%}

.contact{padding:70px 40px 40px 40px;min-height:50vh;display:flex;flex-direction:column;align-items:center;justify-content:center}.contact__title{margin-bottom:120px}.contact__row{display:flex;margin:0px -40px}.contact__form{padding:0px 40px;flex:1 1 50%;display:flex;flex-direction:column;align-items:end}.contact__info{padding:0px 40px;flex:1 1 50%;font-size:14px}.contact__text{line-height:150%;margin-bottom:30px}.contact__author{font-weight:bold;margin-bottom:20px}.contact__socials{width:30px;height:30px;display:grid;grid-gap:11px;gap:11px;grid-auto-flow:column;margin-bottom:20px}.contact__item{width:30px;height:30px;border:2px solid black;border-radius:50%;display:flex;align-items:center;justify-content:center;font-weight:bold;font-size:18px;-webkit-user-select:none;-ms-user-select:none;user-select:none;cursor:pointer;transition-duration:0.5s}.contact__item:hover{transform:scale(1.1, 1.1)}.contact__mail span{font-weight:bold}.form{align-items:center;justify-items:center;display:grid;grid-template-columns:1fr 1fr;grid-template-rows:1fr 1fr 1fr;grid-template-areas:"name email" "message message" "button button"}.form__name{grid-area:name}.form__name p{text-transform:uppercase;font-weight:bold;margin-bottom:18px}.form__name input{transition-duration:0.5s;background-color:#F8F7F7;border-top:1px solid black;height:36px}.form__name input:focus{box-shadow:0px 0px 10px rgba(0,0,0,0.3)}.form__email{grid-area:email}.form__email p{text-transform:uppercase;font-weight:bold;margin-bottom:18px}.form__email input{transition-duration:0.5s;background-color:#F8F7F7;border-top:1px solid black;height:36px}.form__email input:focus{box-shadow:0px 0px 10px rgba(0,0,0,0.3)}.form__message{grid-area:message}.form__message p{text-transform:uppercase;font-weight:bold;margin-bottom:18px}.form__message textarea{transition-duration:0.5s;background-color:#F8F7F7;border-top:1px solid black;width:380px;height:120px;resize:none;padding:5px}.form__message textarea:focus{box-shadow:0px 0px 10px rgba(0,0,0,0.3)}.form__button{grid-area:button;justify-self:end}.form__button button{transition-duration:0.5s;text-transform:uppercase;background-color:rgba(0,0,0,0);padding:50px;font-weight:bold}.form__button button:hover{transform:scale(1.1, 1.1)}.form__button button:active{color:rgba(0,0,0,0.5)}

.footer{padding:40px;background-color:#3C3C3C;display:flex;flex-direction:column;align-items:center}.footer__logo{margin-bottom:40px;width:122px;height:78px}.footer__logo img{max-width:100%}.footer__menu{margin-bottom:34px}.footer__list{color:#A4A4A4;display:flex}.footer__list li{margin-right:30px;cursor:pointer;-webkit-user-select:none;-ms-user-select:none;user-select:none;transition-duration:0.5s}.footer__list li:hover{transform:scale(1.1, 1.1)}.footer__list li:last-child{margin-right:0}.footer__copy{color:#6C6C6C}

