.wrapper {
  min-width: 100vh;
  display: flex;
  flex-direction: column;
  //align-items: center;

  font-family: "Lato", sans-serif;
}

.title {
  font-size: 34px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 8.95px;
  position: relative;
  text-align: center;
  padding: 0px 0px 30px 0px;

  h2 {
    &:after {
      content: '';
      position: absolute;
      width: 90px;
      height: 2px;
      background-color: black;
      bottom: 0;
      left: 50%;
      margin: 0px 0px 0px -50px;
    }
  }
}

.button {
  button {
    display: block;
    border: 2px solid white;
    width: 200px;
    height: 50px;
    //margin: 0 auto;
    background-color: rgba(0,0,0,0);
    color: white;
    position: relative;
  }
}